import React from "react";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import Nav from "../../components/Navbar/Nav";
import { useState, useEffect } from "react";
import { Select } from "antd";
import toast from "react-hot-toast";
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
const { Option } = Select;

const Updateproduct = () => {
  const [categories, Setcategories] = useState([]);
  const [photo, Setphoto] = useState("");
  const [productName, SetproductName] = useState("");
  const [description, Setdescription] = useState("");
  const [category, Setcategory] = useState("");
  const [quantity, Setquantity] = useState("");
  const [price, Setprice] = useState("");
  const [shipping, Setshipping] = useState("");
  const [id, Setid] = useState("");

  const params = useParams();
  const Navigate = useNavigate();

  const getSingleProduct = async () => {
    try {
      const { data } = await axios.get(
        `/api/admin/product/single-product/${params.slug}`
      );

      SetproductName(data.product.productName);
      Setdescription(data.product.description);
      
      Setid(data.product._id);
      Setcategory(data.product.category._id);
      Setquantity(data.product.quantity);
      Setprice(data.product.price);
      Setshipping(data.product.shipping);
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };
  useEffect(() => {
    getSingleProduct();
    //eslint-disable-next-line
  }, []);

  const HandleUpdate = async (e) => {
    e.preventDefault();
    try {
      const productData = new FormData();
      productData.append("productName", productName);
      productData.append("description", description);
      productData.append("category", category);
      productData.append("quantity", quantity);
      productData.append("price", price);
      productData.append("shipping", shipping);
      photo && productData.append("photo", photo);

      const { data } = await axios.put(
        `/api/admin/product/update-product/${id}`,
        productData
      );
      if (data?.success) {

        toast.success("product updated successfully");
        Navigate("/profile/admin/products");
      } else {
        toast.error("product cannot update");
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };

  const HandleDelete = async (e) => {
    try {
      let answer = window.prompt("are you sure to delete the product type 'YES DELETE' ");


      if (answer ==="YES DELETE") {
        const { data } = await axios.delete(
          `/api/admin/product/deleteproduct-photo/${id}`
        );
        if (data?.success) {
          toast.success(data.message);
          Navigate("/profile/admin/products");
        } else {
          toast.error(data.message);
        }
      } else {
        return;
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };
  const getCategories = async () => {
    try {
      const { data } = await axios.get("/api/admin/category/get-category");
      if (data?.success) {
        Setcategories(data?.categories);
        toast.success("categories accquired successfully");
      }
    } catch (error) {
      console.log(error);
      toast.error("cannot get categories");
    }
  };
  useEffect(() => {
    getCategories();
  }, []);
  return (
    <div>
      <Nav />
      <div className="createProduct_main">
        <hr />
        <div className="d-flex row container-fluid p-3 ">
          <div className="col-md-3">
            <AdminMenu />
          </div>
          <div className="col-md-9 ps-5  ">
            <div className=" p-3 w-75 ps-5 ">
              <h1 className="mb-3 font-monospace text-uppercase fw-bold">update products</h1>
              <div className="m-1 w-75">
                <h4 className="fw-bolder ps-1">category:</h4>
                <Select
                  bordered={false}
                  size="large"
                  showSearch
                  value={category ? category : "choose a category darling!"}
                  className="form-select mb-3"
                  onChange={(value) => {
                    Setcategory(value);
                  }}
                >
                  {categories?.map((c) => (
                    <Option key={c._id} value={c._id}>
                      {c.name}
                    </Option>
                  ))}
                </Select>
                <div className="mb-3">
                  <label className="btn btn-outline-primary  py-3 col-md-12"  >
                    {photo ? photo.name : "UPLOAD NEW PHOTO"}
                    <input
                      type="file"
                      name="photo"
                      accept="image/*"
                      className="form-control  "
                      onChange={(e) => Setphoto(e.target.files[0])}
                      hidden
                      // value={photo.name}
                    
                    />
                  </label>
                </div>
                <div className="mb-3">
                  {photo ? (
                    <div className="text-center">
                      <img
                        src={URL.createObjectURL(photo)}
                        alt=" product_photo"
                        height={"200px"}
                        className="img img-responsive rounded-3"
                      />
                    </div>
                  ) : (
                    <div className="text-center">
                      <img
                        src={`/api/admin/product/product-photo/${id}`}
                        alt=" product_photo"
                        height={"200px"}
                        className="img img-responsive rounded-3"
                      />
                    </div>
                  )}
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    value={productName}
                    className="form-control py-3"
                    placeholder=" Product's Name"
                    onChange={(e) => {
                      SetproductName(e.target.value);
                    }}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    value={description}
                    className="form-control py-3"
                    placeholder="About the product"
                    onChange={(e) => {
                      Setdescription(e.target.value);
                    }}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="number"
                    value={quantity}
                    className="form-control py-3"
                    placeholder="No  of Quantity"
                    onChange={(e) => {
                      Setquantity(e.target.value);
                    }}
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="number"
                    value={price}
                    className="form-control py-3"
                    placeholder="Price in rupees"
                    onChange={(e) => {
                      Setprice(e.target.value);
                    }}
                  />
                </div>

                <Select
                  bordered={false}
                  placeholder="select shipping"
                  size="large"
                  showSearch
                  value={shipping?"yes":"no"}
                  className="form-select mb-3 "
                  onChange={(value) => {
                    Setshipping(value);
                  }}
                >
                  <Option value="0"> no</Option>
                  <Option value="1">yes</Option>
                </Select>
                <div className="d-flex gap-5 colmun container-fluid ">
                  <div className="mb-3 ms-5">
                    <button
                      type="submit"
                      className="btn btn-primary px-4 py-3"
                      onClick={HandleUpdate}
                    >
                      UPDATE PRODUCT
                    </button>
                  </div>

                  <div className="mb-3 ms-5">
                    <button
                      className="btn btn-danger px-4 py-3"
                      onClick={HandleDelete}
                    >
                      DELETE PRODUCT
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Updateproduct;
